import React from "react";
import {Row, Col, Button, Image} from 'react-bootstrap';
import "./StartCard.css";

const StartCard = ({onFinish}) => {

    const getOptions = () => {
        let characterNum = Math.floor(Math.random() * 5);
        let characterNum2 = Math.floor(Math.random() * 5);
        while (characterNum === characterNum2) {
            characterNum2 = Math.floor(Math.random() * 5);
        }
        return [characterNum, characterNum2];
    }

    const getCharImage = (charNum) => {
        switch(charNum) {
            case 0:
                return <Image src="https://i.imgur.com/eDkB9Gg.jpg" rounded onClick={() => setSelectedChar(0)}
                style={selectedChar === charNum ? {border: "6px dashed green"} : null}/>
            case 1:
                return <Image src="https://i.imgur.com/C57cbak.jpg" rounded onClick={() => setSelectedChar(1)}
                style={selectedChar === charNum ? {border: "6px dashed green"} : null}/>
            case 2:
                return <Image src="https://i.imgur.com/PTPZ5Ag.jpg" rounded onClick={() => setSelectedChar(2)}
                style={selectedChar === charNum ? {border: "6px dashed green"} : null}/>
            case 3:
                return <Image src="https://i.imgur.com/RGESQ5a.jpg" rounded onClick={() => setSelectedChar(3)}
                style={selectedChar === charNum ? {border: "6px dashed green"} : null}/>
            default:
                return <Image src="https://i.imgur.com/tfRfOze.jpg" rounded onClick={() => setSelectedChar(4)}
                style={selectedChar === charNum ? {border: "6px dashed green"} : null}/>
        }
    }

    let [magicNum, setMagicNum] = React.useState(Math.floor(Math.random() * 99 + 1));
    let [options, setOptions] = React.useState(getOptions());
    let [team, setTeam] = React.useState(true);
    let [firstTurn, setFirstTurn] = React.useState(false);
    let [selectedChar, setSelectedChar] = React.useState(options[0]);

    const finish = () => {
        onFinish(magicNum, team, firstTurn, selectedChar);
    }
    
    return <div className="eightBit">
        <Row>
            <Col>
                <h2 className="magicNum">{magicNum}</h2>
            </Col>
            <Col>
                <h5>Go first?</h5>
                <Button variant={firstTurn ? "success" : "outline-success"} onClick={() => setFirstTurn(true)}>
                    Yes
                </Button>
                <Button variant={firstTurn ? "outline-danger" : "danger"} onClick={() => setFirstTurn(false)}>
                    No
                </Button>
            </Col>
        </Row>
        <Row>
            {getCharImage(options[0])}
        </Row>
        <Row>
            {getCharImage(options[1])}
        </Row>
        <Row>
            <Button variant={team ? "primary" : "outline-primary"} onClick={() => setTeam(true)} className="my-1" style={{width: "90%", margin: "0 auto"}}>
                Blue
            </Button>
            <Button variant={team ? "outline-danger" : "danger"} onClick={() => setTeam(false)} className="my-1" style={{width: "90%", margin: "0 auto"}}>
                Red
            </Button>
        </Row>
        <Row>
            <Button size="lg" onClick={finish}>Finish!</Button>
        </Row>
    </div>;
}

export default StartCard;