import React from "react";
import {Button, Card} from 'react-bootstrap';
import "../StartCard/StartCard.css";

const StartCard = ({setStep}) => {

    const updateParent = () => {
        setStep(2);
    }

    return <div className="text-center m-5 eightBit">
        <h1 style={{color: "red", fontSize: "48px"}}>Pixel</h1><h1 style={{color: "blue", fontSize: "48px"}}> Parade</h1>
        <hr />
        <Button variant="success" size="lg" onClick={updateParent}>
            Start!
        </Button>
    </div>;
}

export default StartCard;