import React from "react";
import {Container, Row, Col} from 'react-bootstrap';
import StartCard from './components/StartCard/StartCard';
import StartScreen from './components/StartScreen/StartScreen';
import CharacterCard from './components/characterCard/CharacterCard';

const App = () => {

  let [step, setStep] = React.useState(1);
  let [magicNum, setMagicNum] = React.useState();
  let [team, setTeam] = React.useState(true);
  let [firstTurn, setFirstTurn] = React.useState(true);
  let [selectedChar, setSelectedChar] = React.useState();

  const finish = (magicNum, team, firstTurn, selectedChar) => {
    setMagicNum(magicNum);
    setTeam(team);
    setFirstTurn(firstTurn);
    setSelectedChar(selectedChar);
    setStep(3);
  }

  const renderApp = (currentStep) => {
    if (currentStep === 1) {
      return <StartScreen setStep={setStep} />;
    }
    else if (currentStep === 2) {
      return <StartCard onFinish={finish} />;
    }
    else
      return <CharacterCard charNum={selectedChar} magicNum={magicNum} first={firstTurn} team={team} />;
  }

  return <Container style={{height: "100%"}}>
    <Row>
        <Col>
            {renderApp(step)}
        </Col>
    </Row>
  </Container>

}

export default App;
