import React from "react";
import {Row, Image} from 'react-bootstrap';
import "./CharacterCard.css";

const CharacterCard = ({charNum, magicNum, first, team}) => {

    const getChar = () => {
        switch(charNum) {
            case 0:
                return <Image src="https://i.imgur.com/eDkB9Gg.jpg" rounded/>
            case 1:
                return <Image src="https://i.imgur.com/C57cbak.jpg" rounded/>
            case 2:
                return <Image src="https://i.imgur.com/PTPZ5Ag.jpg" rounded/>
            case 3:
                return <Image src="https://i.imgur.com/RGESQ5a.jpg" rounded/>
            default:
                return <Image src="https://i.imgur.com/tfRfOze.jpg" rounded/>
        }
    }

    return <div style={{backgroundColor: team ? "blue" : "red", paddingBottom: "100%"}}>
        <Row>
            <h3 className="eightBit magicNum">{magicNum}</h3>
            {first ? <h3 className="eightBit" style={{color: "orange"}}>"Trying to go first!"</h3> : <></>}
        </Row>
        <Row>
            {getChar()}
        </Row>
    </div>
}

export default CharacterCard;